import React, { useState } from 'react'
import { Nav, Container, Navbar } from 'react-bootstrap'

const Charge = (props) => {

  let {charge, setCharge} = props

  return (
    <div className='charge'>
      <Navbar bg="primary" data-bs-theme="dark" className='Charge_Nav'>
        <Container>
          <Navbar.Brand href="#home">결제창</Navbar.Brand>
          <Nav className="me-auto">
          </Nav>
          <div className="Charge_x">
            <button onClick={()=>{
              setCharge(false)
            }}>X</button>
          </div>
        </Container>
      </Navbar>
      <h4>결제창 입니다.</h4>
    </div>
  )
}

export default Charge