import React, { useEffect, useRef, useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import data from '../data.js'
import Product from '../components/Product.jsx';
import { useNavigate } from 'react-router-dom';

const Detail = (props) => {
    let origin = data;
    const {mouse, setMouse}=props;

    let navigate = useNavigate()
    

  return (
    <>
    <div id="Detail_Banner">
        <div className="Detail_Banner_Text">
            <h1>마우스</h1>
            <p>나에게 맞는 마우스 선택하기</p>
        </div>
    </div>
    <div id="Detail_Product">
        <div className="Detaill_Product_Wrap">
            <div className="Detail_Product_Filter">
                <ul className='Serise_Filter'>
                    <li>
                        <input type="checkbox" id="check1" value='M-Serise'
                         onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "M_Series"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                        <label for="check1"></label><span>M-Serise</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check2' value='MX-Serise'
                        onChange={(e)=>{                            
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "MX_Series"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                        <label for="check2"></label><span>MX-Serise</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check3' value='ERGO-Serise' onChange={(e)=>{
                            
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "ERGO_Series"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                        <label for="check3"></label><span>ERGO-Serise</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check4' value='Basic-Serise' onChange={(e)=>{
                            
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "BASIC_Series"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                        <label for="check4"></label><span>Basic-Serise</span>
                    </li>
                </ul>
                <div className="Filter_Line">

                </div>
                <div className="Hands_Filter_Box">
                    <ul className='Hands_Filter'>
                        <li>
                            <input type="checkbox" id="check5" value='큼' onChange={(e)=>{
                                
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.hands === "큼"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                            <label for="check5"></label><span>큼</span>
                        </li>
                        <li>
                            <input type="checkbox" id="check6" value='보통' onChange={(e)=>{
                                
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.hands === "보통"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                            <label for="check6"></label><span>보통</span>
                        </li>
                        <li>
                            <input type="checkbox" id="check7" value='작음' onChange={(e)=>{
                                
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.hands === "작음"
                                })
                                setMouse(copy)
                            } else {
                                let copy = [...origin]
                                setMouse(copy)
                            }
                        }}/>
                            <label for="check7"></label><span>작음</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="Detail_Product_Mouse">
                <Container className='my-5'>
                    <Row>
                        {
                            mouse.map((item, i) => 
                                <Col md={4} key={i} className='my-5 text-center Col-Cursor' onClick={()=>{navigate('/product/mouse/'+item.id)}}>
                                    <img src={process.env.PUBLIC_URL+item.imgUrl} width="70%" className='bg-light' alt=''/>
                                    <h4 className='my-3 text-center'>{item.title}</h4>
                                    <h6 className='my-3 text-center'>{item.price}</h6>
                                </Col>
                            )
                        }
                    </Row>
                </Container>
            </div>
        </div>
    </div>
    </>
  )
}

export default Detail