import React, { useState } from 'react'
import { Button, Container, Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { addCount, removeCount } from '../store';
import Charge from '../routes/Charge';


const Cart = () => {

    let user=useSelector(state=>state.user);
    let cart = useSelector(state=>state.cart);
    let {name} = user;
    let dispatch = useDispatch()
    let [charge, setCharge] = useState(false)
    let totalPrice = ()=>{
        let total = 0
        for(let i in cart) {
            total += cart[i].price*cart[i].amount
        } 
        return total
    }
 
  return (
    <>
        <div id="Cart_wrap">
            <Container>
                <h5>{user.name}의 장바구니</h5>
                <Table className='text-center'>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>상품명</th>
                            <th colSpan="3">수량</th>
                            <th>가격</th>
                        </tr>
                    </thead>
                    <tbody>

                        {
                            cart.map((v, i) =>
                                <tr key={i}>
                                    <td>{v.id}</td>
                                    <td>{v.item}</td>
                                    <td>
                                        <button onClick={()=>{
                                        dispatch(addCount(v.id))
                                        }}>+</button>
                                    </td>
                                    <td>{v.amount}</td>
                                    <td><button onClick={()=>{
                                        dispatch(removeCount(v.id))
                                        }}>-</button>
                                    </td>
                                    <td>{v.price*v.amount}</td>
                                </tr>
                            )
                        }
                    </tbody>
                </Table>
                <div className="Cart_total text-end">
                    <p>총 가격 : {totalPrice()}</p>
                    <Button variant="danger" onClick={()=>{
                        setCharge(!charge)
                    }}>구매하기</Button>
                </div>
            </Container>
            {
                charge === true ? <Charge charge={charge} setCharge={setCharge}/> : null
            }
        </div>
    </>
  )
}

export default Cart