import React, { useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import data3 from '../data3.js'
import Product from '../components/Product.jsx';
import { useNavigate } from 'react-router-dom';

const Etc = (props) => {
    let origin = data3;
    const {etc, setEtc}=props;
    let navigate = useNavigate()

  return (
    <>
    <div id="Etc_Banner">
        <div className="Etc_Banner_Text">
            <h1>주변기기</h1>
            <p>현재 필요한 주변기기 살펴보기</p>
        </div>
    </div>
    <div id="Keyboard_Product">
        <div className="Detaill_Product_Wrap">
            <div className="Detail_Product_Filter">
                <ul className='Serise_Filter'>
                    <li>
                        <input type="checkbox" id="check1" value='Web-Cam' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "Web_Cam"
                                })
                                setEtc(copy)
                            } else {
                                let copy = [...origin]
                                setEtc(copy)
                            }
                        }}/>
                        <label for="check1"></label><span>WEB-CAM</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check2' value='HEADSET' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "HEADSET"
                                })
                                setEtc(copy)
                            } else {
                                let copy = [...origin]
                                setEtc(copy)
                            }
                        }}/>
                        <label for="check2"></label><span>HEADSET</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check3' value='BLUETOOTH' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "BLUETOOTH"
                                })
                                setEtc(copy)
                            } else {
                                let copy = [...origin]
                                setEtc(copy)
                            }
                        }}/>
                        <label for="check3"></label><span>BLUETOOTH</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check4' value='SPEAKER' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "SPEAKER"
                                })
                                setEtc(copy)
                            } else {
                                let copy = [...origin]
                                setEtc(copy)
                            }
                        }}/>
                        <label for="check4"></label><span>SPEAKER</span>
                    </li>
                </ul>
                <div className="Filter_Line">

                </div>
                <div className="Hands_Filter_Box">
                    <ul className='Hands_Filter'>
                        <li>
                            <input type="checkbox" id="check5" value='C-Type' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.option === "C-Type"
                                })
                                setEtc(copy)
                            } else {
                                let copy = [...origin]
                                setEtc(copy)
                            }
                        }}/>
                            <label for="check5"></label><span>C-Type</span>
                        </li>
                        <li>
                            <input type="checkbox" id="check6" value='A-Type' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.option === "A-Type"
                                })
                                setEtc(copy)
                            } else {
                                let copy = [...origin]
                                setEtc(copy)
                            }
                        }}/>
                            <label for="check6"></label><span>A-Type</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="Detail_Product_Etc">
                <Container className='my-5'>
                    <Row>
                        {
                            etc.map((item, i) =>
                                <Col md={4} className='my-5 text-center Col-Cursor Etc_box' onClick={()=>{navigate('/product/etc/'+item.id)}}>
                                    <div className="keyboard_img_box text-center bg-light">
                                        <img src={process.env.PUBLIC_URL+item.imgUrl} width="100%" className='bg-light' alt=''/>
                                    </div>
                                    <h4 className='my-3 text-center'>{item.title}</h4>
                                    <h6 className='my-3 text-center'>{item.price}</h6>
                                </Col>
                            )
                        }
                    </Row>
                </Container>
            </div>
        </div>
    </div>
    </>
  )
}

export default Etc