import { configureStore, createSlice } from '@reduxjs/toolkit'

let user = createSlice({
    name: 'user',
    initialState: {name: 'hwan', age:20},
    reducers:{
        changeName(state){
            state.name='hwan chae'
        },
        increase(state,action){
            state.age+=action.payload
        }
    }
})
export let {changeName, increase}=user.actions

let cart= createSlice({
    name:'cart',
    initialState:[
    ],
    reducers : {
        addCount(state, action) {
            let idx=state.findIndex((a)=>{return a.id===action.payload})
            ++state[idx].amount;
        },
        removeCount(state, action) {
            let idx=state.findIndex((a)=>{return a.id===action.payload})
            --state[idx].amount;
        },
        addItem(state, action){
            let idx=state.findIndex((a)=>{return a.id===action.payload.id})
            if(idx>=0){
                ++state[idx].amount
            }else{
            state.push(action.payload)
        }
        },
        sortName(state){
            state.sort((a,b)=>
                a.item > b.item ? 1: -1,
            );
        }
    }
})

export let {addCount, removeCount ,addItem, sortName} = cart.actions

export default configureStore({
  reducer: {
    user : user.reducer,
    cart : cart.reducer
   }

}) 