import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.scss';
import {Container, Nav, Navbar, NavDropdown,Row, Col, NavLink} from 'react-bootstrap'
import { Routes, Route, Link, useNavigate, useParams } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import Detail from './routes/Detail';
import Keyboard from './routes/Keyboard'
import { Navigation, Pagination, Mousewheel} from 'swiper/modules';
import Home from './components/Home';
import Product from './components/Product';
import Etc from './routes/Etc'
import data from './data.js'
import data2 from './data2.js';
import data3 from './data3.js';
import './Pulgin/style.css';
import Cart from './components/Cart';


function App() {
  const navigate = useNavigate()
  let [mouse, setMouse] = useState(data)
  let [keyboard, setKeyboard] = useState(data2)
  let [etc, setEtc] = useState(data3)

  const [scrollPosition, setScrollPosition] = useState(0);

  const updateScroll=() => {
    setScrollPosition(window.scrollY || document.documentElement.scrollTop);
  };

  useEffect(()=>{
    window.addEventListener("scroll", updateScroll);
  },[]);



  return (
    <div className="App">
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand href="#home">
            <img src={process.env.PUBLIC_URL+'/img/logitechg-grey.svg'} alt='logo' />
          </Navbar.Brand>
          <Nav className="me-auto">
            <NavDropdown title="우리 브랜드" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">
                <img src='/img/streamlabs-grey.svg' alt='streamlabs' />
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                <img src='/img/ultimate-ears-grey.svg' alt='ultimate-ears' />
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">
                <img src='/img/jaybird-grey.svg' alt='jaybird-grey' />
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <i className="fa-solid fa-earth-asia Navi"><span>대한민국</span></i>
        </Container>
      </Navbar>

      <div id="Menu_Bar">
        <div className={scrollPosition > 150 ? "menubar_wrap fixed_scroll" : "menubar_wrap"}>
          <div className="menubar">
            <div className="mebar melogo">
              <div className={scrollPosition > 150 ? "fixed_ani" : "melogo_ani"} onClick={()=>{
                navigate('/')
              }}>
              </div>
            </div>
            <div className="mebar meul">
              <ul className={scrollPosition > 150 ? "me_ul fixed_me_ul" : 'me_ul'}>
                <li className='dropDown'>
                  <span>제품</span>
                 <div className='add_meul'>
                    <div className="add_meul_wrap">
                      <div className="mouse_box">
                        <h6 onClick={() => {
                          navigate('/detail')
                          }}>마우스</h6>
                      </div>
                      <div className="keyboard_box">
                        <h6 onClick={() => {
                          navigate('/keyboard')
                          }}>키보드</h6>
                      </div>
                      <div className="etc_box">
                        <h6 onClick={() => {
                          navigate('/etc')
                          }}>웹캠 및 오디오</h6>
                      </div>
                    </div>
                  </div> 
                </li>
                <li>새로운</li>
                <li>비즈니스</li>
                <li>지원</li>
              </ul>
            </div>
            <div className={scrollPosition > 150 ? "fixed_meicon" : "mebar meicon"}>
              <i className="fa-solid fa-magnifying-glass"></i>
              <i className="fa-regular fa-user"></i>
            </div>
          </div>
        </div>
      </div>

      <Routes>
        <Route path='/' element={<Home/>}/>
        <Route path='/detail' element={<Detail mouse={mouse} setMouse={setMouse}/>}/>
        <Route path='/keyboard' element={<Keyboard keyboard={keyboard} setKeyboard={setKeyboard}/>}/>
        <Route path='/etc' element={<Etc etc={etc} setEtc={setEtc}/>}/>
        <Route path='/product/mouse/:id' element={<Product item={mouse}/>}/>
        <Route path='/product/keyboard/:id' element={<Product item={keyboard}/>}/>
        <Route path='/product/etc/:id' element={<Product item={etc}/>}/>
        <Route path='/cart' element={<Cart/>}/>
      </Routes>


      <footer>
        <div className="footer_wrap">
          <div className="footerbox1">
            <span>정보</span>
            <a href="#">로지텍 스토리</a>
            <a href="#">채용</a>
            <a href="#">투자자</a>
            <a href="#">블로그</a>
            <a href="#">프레스</a>
            <a href="#">연락처</a>
          </div>
          <div className="footerbox2">
            <span>가치</span>
            <a href="#">사회 공헌</a>
            <a href="#">지속 가능성</a>
            <a href="#">재활용</a>
          </div>
          <div className="footerbox3">
            <span>고객</span>
            <a href="#">이메일 설정</a>
          </div>
          <div className="footer_line">
            <div className="line_icon_box">
              <i className="fa-brands fa-facebook"></i>
              <i className="fa-brands fa-twitter"></i>
              <i className="fa-brands fa-instagram"></i>
            </div>
            <div className="line_search_box">
              <div className="input-group mb-3">
                <input type="text" className="form-control" placeholder="Search..." aria-label="Search..." aria-describedby="button-addon2" />
                <button className="btn btn-outline-secondary" type="button" id="button-addon2">
                  <i className="fa-solid fa-play"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default App;
