import React, { useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import data2 from '../data2.js'
import Product from '../components/Product.jsx';
import { useNavigate } from 'react-router-dom';

const Keyboard = (props) => {
    let origin = data2;
    const {keyboard, setKeyboard}=props;
    let navigate = useNavigate()

  return (
    <>
    <div id="Keyboard_Banner">
        <div className="Keyboard_Banner_Text">
            <h1>키보드</h1>
            <p>나에게 맞는 키보드 선택하기</p>
        </div>
    </div>
    <div id="Keyboard_Product">
        <div className="Detaill_Product_Wrap">
            <div className="Detail_Product_Filter">
                <ul className='Serise_Filter'>
                    <li>
                        <input type="checkbox" id="check1" value='MX-Serise' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "MX_Series"
                                })
                                setKeyboard(copy)
                            } else {
                                let copy = [...origin]
                                setKeyboard(copy)
                            }
                        }}/>
                        <label for="check1"></label><span>MX-Serise</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check2' value='ERGO-Serise' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "ERGO_Series"
                                })
                                setKeyboard(copy)
                            } else {
                                let copy = [...origin]
                                setKeyboard(copy)
                            }
                        }}/>
                        <label for="check2"></label><span>ERGO-Serise</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check3' value='BASIC-Serise' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "BASIC_Series"
                                })
                                setKeyboard(copy)
                            } else {
                                let copy = [...origin]
                                setKeyboard(copy)
                            }
                        }}/>
                        <label for="check3"></label><span>BASIC-Serise</span>
                    </li>
                    <li>
                        <input type="checkbox" id='check4' value='K-Serise' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.content === "K_Series"
                                })
                                setKeyboard(copy)
                            } else {
                                let copy = [...origin]
                                setKeyboard(copy)
                            }
                        }}/>
                        <label for="check4"></label><span>K-Serise</span>
                    </li>
                </ul>
                <div className="Filter_Line">

                </div>
                <div className="Hands_Filter_Box">
                    <ul className='Hands_Filter'>
                        <li>
                            <input type="checkbox" id="check5" value='유선' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.option === "유선"
                                })
                                setKeyboard(copy)
                            } else {
                                let copy = [...origin]
                                setKeyboard(copy)
                            }
                        }}/>
                            <label for="check5"></label><span>유선</span>
                        </li>
                        <li>
                            <input type="checkbox" id="check6" value='무선' onChange={(e)=>{
                            if (e.target.checked === true) {
                                let copy = [...origin].filter((v) => {
                                    return v.option === "무선"
                                })
                                setKeyboard(copy)
                            } else {
                                let copy = [...origin]
                                setKeyboard(copy)
                            }
                        }}/>
                            <label for="check6"></label><span>무선</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="Detail_Product_Key">
                <Container className='my-5'>
                    <Row>
                        {
                            keyboard.map((item, i) =>
                                <Col md={4} className='my-5 text-center Col-Cursor keyboard_box' onClick={()=>{navigate('/product/keyboard/'+item.id)}}>
                                    <div className="keyboard_img_box text-center bg-light">
                                        <img src={process.env.PUBLIC_URL+item.imgUrl} width="100%" alt=''/>
                                    </div>
                                    <h4 className='my-3 text-center'>{item.title}</h4>
                                    <h6 className='my-3 text-center'>{item.price}</h6>
                                </Col>
                            )
                        }
                    </Row>
                </Container>
            </div>
        </div>
    </div>
    </>
  )
}

export default Keyboard