import React from 'react'
import { Button, Col, Row } from 'react-bootstrap'
import { useNavigate, useParams } from 'react-router-dom';
import {addCount, addItem} from "./../store.js";
import { useDispatch } from 'react-redux';

const Product = (props) => {
  const {id} = useParams()
  console.log(id, props)
  // const {mouse, setMouse, keyboard, setKeyboard, etc, setEtc}=props;
  let {item} = props;
  let navigate = useNavigate()
  let dispatch = useDispatch()

  return (
    <>

          <div id="Product_item_Banner">
            <div className="item_wrap">
              <div className="item_img">
                <img src={process.env.PUBLIC_URL+item[id].imgUrl} width="70%" className='text-center' alt='' />
              </div>
              <div className="info_wrap">
                <h4 className='my-3'>상품명 : {item[id].title}</h4>
                <h5 className='my-3'>Serise : {item[id].content}</h5>
                <h5 className='my-3 text-end'>Size : {item[id].hands}</h5>
                <h5 className='my-3 text-end'>유/무선 : {item[id].option}</h5>
                <h5 className='my-3 text-end'>가격 : {item[id].price}</h5>
                <div className="info_button">
                <Button variant="primary" onClick={()=>{
                  navigate('/cart'), dispatch(addItem(
                    {id: item[id].id, item: item[id].title,
                       amount:1, price: item[id].price}))
                }}>구매하기</Button>{' '}
                <Button variant="danger" onClick={()=>{
                  navigate('/cart'), 
                  dispatch(addItem(
                    {id: item[id].id, item: item[id].title,
                       amount:1, price: item[id].price}))
                }}>장바구니</Button>
                </div>
              </div>
            </div>
          </div>

    </>
  )
}

export default Product